import { Box, Button, Container, Divider, Grid, IconButton, InputAdornment, TextField, Typography } from '@mui/material';
import React, { useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';
import MuiAlert from '@mui/material/Alert';
import { Snackbar } from '@mui/material';
import axios from 'axios';

const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />
});

function ResetPass() {

    const navigate = useNavigate()
    const {id} = useParams()
    const token = localStorage.getItem('token')
    const [change, setChange] = useState(false)
    const [newPass, setNewPass] = useState('')
    const [customVariant, setCustomVariant] = React.useState('success')
    const [open, setOpen] = React.useState(false);
    const [error, setError] = React.useState('')
    const [success, setSuccess] = React.useState('')
    const [user, setUser] = useState({
      oldPass: '',
      newPass: '',
      conPass: '',
    })
    const [valErr, setValErr] = React.useState({
        oldPass: '',
        newPass: '',
        conPass: '',
    })
    const [show, setShow] = useState({
      oldShow: false,
      newShow: false,
      conShow: false
    })

    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
          return;
        }
        setOpen(false);
    }

    const handleOnChange = (evt) => {
        setUser({
          ...user,
          [evt.target.name]: evt.target.value
        })
    
        setValErr({
          oldPass: '',
          newPass: '',
          conPass: '',
        })
    }

    // Authorization: Bearer

    const handleSubmit = async(evt) => {
        evt.preventDefault();
        if(!user.oldPass){
          setValErr({oldPass: 'Please enter your old password!'})
        }else if(!user.newPass){
          setValErr({newPass: 'Please enter new password!'})
        }else if(!user.conPass){
          setValErr({conPass: 'Please enter confirm password!'})
        }else if(user.newPass !== user.conPass){
          setValErr({conPass: 'Confirm password not matched!'})
        }else if(user.oldPass === user.newPass && user.oldPass === user.conPass){
          setError('New password must be defferent from old password!')
          setCustomVariant('error')
          setOpen(true)
        }else{
          return await axios.post(`http://localhost:4000/users/resetPassword/${id}`, user, {headers: {'Content-Type': 'application/json', 'Authorization': `Bearer ${token}`}}).then((response) => {
            setError('')
            setSuccess(response.data.msg)
            setCustomVariant('success')
            setOpen(true)
            navigate('/')
            setUser({
              oldPass: '',
              newPass: '',
              conPass: ''
            })
          }).catch((err) => {
            setSuccess('')
            setError(err?.response.data.msg)
            setCustomVariant('error')
            setOpen(true)
            // console.log("error to reset password: ", err);
          })
        }
    }

    return (
          <Container>
            <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
              <Alert onClose={handleClose} severity={customVariant} sx={{ width: '100%' }}>
                  {error ? error : success}
              </Alert>
            </Snackbar>
            <Box component={'form'} noValidate onSubmit={handleSubmit} sx={{ mt: 3}}>
                <Grid container spacing={2}>
                  <Grid item xs={12}>
                    
                    <Typography variant='body1' component={'p'} sx={{ fontSize: '18px', fontFamily: 'cursive', fontWeight: 600, mb: 2}}>Confirm your password by using old password!</Typography>
                    
                    <TextField error={valErr.oldPass ? true : false} helperText={valErr.oldPass ? valErr.oldPass : ''} name="oldPass" onChange={handleOnChange} required fullWidth label="Old Password" autoFocus type={show.oldShow ? 'text' : 'password'} id="input-with-icon-textfield" InputProps={{ endAdornment: ( 
                        <InputAdornment position="end">
                          <IconButton onClick={() => setShow({...show, oldShow: !show.oldShow})}>
                            {show.oldShow ? <VisibilityOff /> : <Visibility />}
                          </IconButton>
                        </InputAdornment>
                        )
                      }}
                      variant="outlined"
                    />
                  </Grid>
                  
                  <Divider sx={{ mt: 2, mb: 1}} />

                  <Grid item xs={12}>
                    <Typography variant='body1' component={'p'} sx={{ fontSize: '18px', fontFamily: 'cursive', fontWeight: 600, mb: 2, mt: 2}}>Set your new password!</Typography>
                    <TextField  error={valErr.newPass ? true : false} helperText={valErr.newPass ? valErr.newPass : ''} name="newPass" onChange={handleOnChange} required fullWidth label="New Password" autoFocus type={show.newShow ? 'text' : 'password'} id="input-with-icon-textfield" InputProps={{ endAdornment: ( 
                        <InputAdornment position="end">
                          <IconButton onClick={() => setShow({...show, newShow: !show.newShow})}>
                            {show.newShow ? <VisibilityOff /> : <Visibility />}
                          </IconButton>
                        </InputAdornment>
                        )
                      }}
                      variant="outlined"
                    />
                  </Grid>

                  <Grid item xs={12}>
                    <Typography variant='body1' component={'p'} sx={{ fontSize: '18px', fontFamily: 'cursive', fontWeight: 600, mb: 2, mt: 2}}>Confirm your password!</Typography>
                    <TextField  error={valErr.conPass ? true : false} helperText={valErr.conPass ? valErr.conPass : ''} name="conPass" onChange={handleOnChange} required fullWidth label="Confirm Password" autoFocus type={show.conShow ? 'text' : 'password'} id="input-with-icon-textfield" InputProps={{ endAdornment: ( 
                        <InputAdornment position="end">
                          <IconButton onClick={() => setShow({...show, conShow: !show.conShow})}>
                            {show.conShow ? <VisibilityOff /> : <Visibility />}
                          </IconButton>
                        </InputAdornment>
                        )
                      }}
                      variant="outlined"
                    />
                  </Grid>

                  <Grid item xs={12}>
                    <center><Button variant='outlined' type='submit' fullWidth style={{ marginTop: '10px'}}>Reset Password</Button></center>
                  </Grid>
                </Grid>
              </Box>
          </Container>
    )
}

export default ResetPass