// import React from 'react'
import * as React from 'react';
import Box from '@mui/material/Box';
import Stepper from '@mui/material/Stepper';
import Step from '@mui/material/Step';
import StepLabel from '@mui/material/StepLabel';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import { Container, Grid, TextField } from '@mui/material';
import { Snackbar } from '@mui/material';
import axios from 'axios'
import { validate } from 'email-validator';
import MuiAlert from '@mui/material/Alert';
import { useNavigate } from 'react-router-dom'

const steps = ['Enter Email to Recover Password!', 'Enter OTP!', 'Create New Password!'];

const Alert = React.forwardRef(function Alert(props, ref) {
    return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />
});

function ForgotPass() {

    // my logic code (vars)
    const navigate = useNavigate()
    const [customVariant, setCustomVariant] = React.useState('success')
    const [open, setOpen] = React.useState(false);
    const [email, setEmail] = React.useState('')
    const [otp, setOtp] = React.useState(Number)
    const [pass, setPass] = React.useState({
        newPass: '',
        conPass: ''
    })
    const [id, setId] = React.useState('')
    const [error, setError] = React.useState('')
    const [success, setSuccess] = React.useState('')
    const [valErr, setValErr] = React.useState({
        email: '',
        otp: '',
        newPass: '',
        conPass: ''
    })

    // end my logic code (vars)


    // design code (vars, functions)

    const [activeStep, setActiveStep] = React.useState(0);
    const [skipped, setSkipped] = React.useState(new Set());

    const isStepOptional = (step) => {
        return step === 1;
    };

    const isStepSkipped = (step) => {
        return skipped.has(step);
    };

    const handleNext = () => {
        let newSkipped = skipped;
        if (isStepSkipped(activeStep)) {
            newSkipped = new Set(newSkipped.values());
            newSkipped.delete(activeStep);
        }

        setActiveStep((prevActiveStep) => prevActiveStep + 1);
        setSkipped(newSkipped);
    };

    const handleBack = () => {
        setActiveStep((prevActiveStep) => prevActiveStep - 1);
        if(activeStep == 0){
            setOtp('')
            setId('')
            setPass('')
        }
    };

    /* const handleSkip = () => {
        if (!isStepOptional(activeStep)) {
        throw new Error("You can't skip a step that isn't optional.");
        }

        setActiveStep((prevActiveStep) => prevActiveStep + 1);
        setSkipped((prevSkipped) => {
        const newSkipped = new Set(prevSkipped.values());
        newSkipped.add(activeStep);
        return newSkipped;
        });
    }; */

    const handleReset = () => {
        setActiveStep(0);
    };

    // end of degin of setpper



    // my logic code (functions)


    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
          return;
        }
        setOpen(false);
    }

    const handleOnChange = (evt) => {
        setPass({
          ...pass,
          [evt.target.name]: evt.target.value
        })
    
        setValErr({
          email: '',
          otp: '',
          newPass: '',
          conPass: ''
        })
      }

    const sendEmailToOTP = async(evt) => {
        evt.preventDefault();

        if(!email){
            setValErr({email: "Please enter email!"})
        }else if(!validate(email)){
            setValErr({email: 'Enter valid email!'})
        }else{
            return await axios.post(`http://localhost:4000/users/forgotPassword`, {email}).then((response) => {
                setError('')
                setSuccess(response.data.msg)
                setCustomVariant('success')
                setOpen(true)
                handleNext()
                setId(response.data.id)
                // console.log("response: ", response);
            }).catch((err) => {
                setSuccess('')
                setError(err.response.data.msg)
                setCustomVariant('error')
                setOpen(true)
            })
        }

    }

    // console.log("email: ", email, id);

    const confirmOTP = async(evt) => {
        evt.preventDefault()

        if(!otp){
            setValErr({otp: 'Please enter OTP!'})
        }else if(otp.length != 4){
            setValErr({otp: 'Please enter valid OTP!'})
        }else{
            return await axios.post(`http://localhost:4000/users/confirmOtp/${id}`, {otp}).then((response) => {
                setError('')    
                setSuccess(response.data.msg)
                setCustomVariant("success")
                setOpen(true)
                handleNext()
            }).catch((err) => {
                setSuccess('')
                setError(err.response.data.msg)
                setCustomVariant('error')
                setOpen(true)
                // console.log("error on otp confirm: ", err.response);
            })
        }
    }

    const confirmPassword = async(evt) => {
        evt.preventDefault()

        if(!pass.newPass){
            setValErr({newPass: 'Please enter new password?'})
        }else if(!pass.conPass){
            setValErr({conPass: 'Please enter confirm password'})
        }else if(pass.newPass.length < 5){
            setValErr({newPass: 'Password must be 5 word long?'})
        }else if(pass.newPass !== pass.conPass){
            setValErr({conPass: 'New password and confirm password are not matched?'})
        }else{
            return await axios.post(`http://localhost:4000/users/confirmPass/${id}`, {otp, pass}).then((response) => {
                setError('')
                setSuccess(response.data.msg)
                setCustomVariant('success')
                setOpen(true)
                navigate('/signin')
            }).catch((err) => {
                setError(err.response.data.msg)
                setCustomVariant('error')
                setOpen(true)
                // console.log("error on confirmPassword: ", err);
            })
        }
    }

    return (
        <Container>
            <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
                <Alert onClose={handleClose} severity={customVariant} sx={{ width: '100%' }}>
                    {error ? error : success}
                </Alert>
            </Snackbar>
            <Box sx={{ width: '100%', mt: 2 }}>
                <Stepper activeStep={activeStep}>
                    {steps.map((label, index) => {
                        const stepProps = {};
                        const labelProps = {};
                        if (isStepOptional(index)) {
                            labelProps.optional = ( <Typography variant="caption">Check Email</Typography> )
                        }
                        if (isStepSkipped(index)) {
                            stepProps.completed = false;
                        }
                        return (
                            <Step key={label} {...stepProps}>
                                <StepLabel {...labelProps}>{label}</StepLabel>
                            </Step>
                        );
                    })}
                </Stepper>
                {activeStep === steps.length ? (
                    <React.Fragment>
                        <Typography sx={{ mt: 2, mb: 1 }}>
                            All steps completed - you&apos;re finished
                        </Typography>
                        <Box sx={{ display: 'flex', flexDirection: 'row', pt: 2 }}>
                            <Box sx={{ flex: '1 1 auto' }} />
                            <Button onClick={handleReset}>Reset</Button>
                        </Box>
                    </React.Fragment>
                ) : (
                    <React.Fragment>

                        {/* Email section */}
                        {activeStep === 0 ? 
                            <Box sx={{ width: '100%', display: 'flex', textAlign: 'center'}} >
                                <Grid container spacing={2}>
                                    <Grid item xs={12}>
                                        <Typography variant='h6' component={'p'} color="primary" sx={{ display: 'block'}} >Enter email to recore your password!</Typography>
                                    </Grid>
                                    <Grid item xs={12}>
                                        <Box component={'form'} noValidate sx={{ display: 'block'}} >
                                            <Grid container spacing={2}>
                                                <Grid item xs={12}>
                                                    <TextField error={valErr.email ? true : false} helperText={valErr.email ? valErr.email : ''} required fullWidth id="email" onChange={(evt) => (setEmail(evt.target.value), setValErr({email: ''}))} label="Email Address" name="email" />
                                                </Grid>
                                                <Grid item xs={12}>
                                                    <Button onClick={sendEmailToOTP} variant='outlined' sx={{width: '50%'}} fullWidth >Send OTP</Button>
                                                </Grid>
                                            </Grid>
                                        </Box>
                                    </Grid>
                                </Grid>
                            </Box> : ('') 
                        }

                        {/* OTP section */}
                        {activeStep === 1 ? 
                            
                            <Box sx={{ width: '100%', display: 'flex', textAlign: 'center'}} >
                                <Grid container spacing={2}>
                                    <Grid item xs={12}>
                                        <Box component={'form'} noValidate sx={{ display: 'block'}} >
                                            <Grid container spacing={2}>
                                                <Grid item xs={12}>
                                                    <TextField error={valErr.otp ? true : false} helperText={valErr.otp ? valErr.otp : ''} required fullWidth id="otp" onChange={(evt) => (setOtp(evt.target.value), setValErr({otp: ''}))} label="OTP" name="otp" type='number' />
                                                </Grid>
                                                <Grid item xs={12}>
                                                    <Button onClick={confirmOTP} variant='outlined' sx={{width: '50%'}} fullWidth >Confirm OTP</Button>
                                                </Grid>
                                            </Grid>
                                        </Box>
                                    </Grid>
                                </Grid>
                            </Box> : ('') 
                        }


                        {/* Password section */}
                        {activeStep === 2 ? 
                            
                            <Box sx={{ width: '100%', display: 'flex', textAlign: 'center'}} >
                                <Grid container spacing={2}>
                                    <Grid item xs={12}>
                                        <Box component={'form'} noValidate sx={{ display: 'block'}} >
                                            <Grid container spacing={2}>
                                                <Grid item xs={12}>
                                                    <TextField error={valErr.newPass ? true : false} helperText={valErr.newPass ? valErr.newPass : ''} required fullWidth name="newPass" onChange={handleOnChange} label="Password" type="password" id="password" />
                                                </Grid>
                                                <Grid item xs={12}>
                                                    <TextField error={valErr.conPass ? true : false} helperText={valErr.conPass ? valErr.conPass : ''} required fullWidth name="conPass" onChange={handleOnChange} label="Confirm Password" type="password" id="conPass" />
                                                </Grid>
                                                <Grid item xs={12}>
                                                    <Button onClick={confirmPassword} variant='outlined' sx={{width: '50%'}} fullWidth >Submit</Button>
                                                </Grid>
                                            </Grid>
                                        </Box>
                                    </Grid>
                                </Grid>
                            </Box> : ('')
                        }

                        {/* Back and Next Button */}
                        <Box sx={{ display: 'flex', flexDirection: 'row', pt: 2 }}>
                            <Button color="inherit" disabled={activeStep === 0} onClick={handleBack} sx={{ mr: 1 }} > Back </Button>
                        </Box>
                    </React.Fragment>
                )}
                </Box>
        </Container>
    )
}

export default ForgotPass