import { Fragment, useEffect, useState } from 'react'
import { Route, Routes, useNavigate } from 'react-router-dom'
import jwtDecoder from 'jwt-decode'
import Home from './Pages/Home'
import SignIn from './Pages/SignIn'
import SignUp from './Pages/SignUp'
import NavBar from './Components/NavBar'
import ResetPass from './Pages/ResetPass'
import ForgotPass from './Pages/ForgotPass'


// 842713881216-hcb825v34b7t2f7gdpsoj974o8g7h1uk.apps.googleusercontent.com

function App() {
  const navigate = useNavigate()
  const token = localStorage.getItem("token")
  
  const [user, setUser] = useState('')
  const [gUser, setGUser] = useState('')

  useEffect(() => {
    const date = new Date()
    if(token){
      const decodedToken = jwtDecoder(token)
      // console.log("decodedToken: ", decodedToken.name);
      if(decodedToken.exp * 1000 < date.getTime()){
        localStorage.removeItem("token")
        navigate('/signin')
      }else{
        setGUser({email: decodedToken.email, name: decodedToken.name, profileUrl: decodedToken.picture})
        setUser(decodedToken )
        navigate('/')
      }
    }else{
      navigate('/signin')
    }
  }, [token]) 

  // console.log("GUser: ", gUser);

  return (
    <Fragment>
      {token ? <>
        <NavBar user={user.result} GUser={gUser} />
        <Routes>
          <Route path='/' element={ <Home /> } />
          <Route path='/regsetPassword/:id' element={ <ResetPass /> } />
        </Routes>
      </> : <>
        <Routes>
          <Route path='/signin' element={ <SignIn /> } />
          <Route path='/signup' element={ <SignUp /> } />
          <Route path='/forgotPassword' element={ <ForgotPass /> } />

        </Routes>
      </>}
    </Fragment>
  );
}

// https://www.youtube.com/watch?v=BnN3TQOG5-g
export default App;