import React from 'react'
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Menu from '@mui/material/Menu';
import MenuIcon from '@mui/icons-material/Menu';
import Container from '@mui/material/Container';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import Tooltip from '@mui/material/Tooltip';
import MenuItem from '@mui/material/MenuItem';
import AdbIcon from '@mui/icons-material/Adb';
import { Link, useNavigate } from 'react-router-dom';
import AccountBalanceIcon from '@mui/icons-material/AccountBalance';
import { Divider } from '@mui/material';

function NavBar({user, GUser}) {
    
    const [anchorElNav, setAnchorElNav] = React.useState(null);
    const [anchorElUser, setAnchorElUser] = React.useState(null);
    const navigate = useNavigate()

    const handleOpenNavMenu = (event) => {
        setAnchorElNav(event.currentTarget);
    };
    const handleOpenUserMenu = (event) => {
        setAnchorElUser(event.currentTarget);
    };

    const handleCloseNavMenu = () => {
        setAnchorElNav(null);
    };

    const handleCloseUserMenu = () => {
        setAnchorElUser(null);
    };

    const logout = () => {
        localStorage.removeItem('token')
        navigate('/signin')
        // console.log("logout");
    }
    
    return (
        <AppBar position="static">
        <Container maxWidth="xl">
            <Toolbar disableGutters>
                <AdbIcon sx={{ display: { xs: 'none', md: 'flex' }, mr: 1 }} />
                <Typography variant="h6" noWrap component="a" href="/" sx={{ mr: 2, display: { xs: 'none', md: 'flex' }, fontFamily: 'monospace', fontWeight: 700, letterSpacing: '.3rem', color: 'inherit', textDecoration: 'none', }} >
                    Auth
                </Typography>

                <Box sx={{ flexGrow: 1, display: { xs: 'flex', md: 'none' } }}>
                    <IconButton size="large" aria-label="account of current user" aria-controls="menu-appbar" aria-haspopup="true" onClick={handleOpenNavMenu} color="inherit" >
                    <MenuIcon />
                        </IconButton>
                    <Menu id="menu-appbar" anchorEl={anchorElNav} anchorOrigin={{ vertical: 'bottom', horizontal: 'left', }} keepMounted
                        transformOrigin={{ vertical: 'top', horizontal: 'left', }}
                        open={Boolean(anchorElNav)} onClose={handleCloseNavMenu} sx={{ display: { xs: 'block', md: 'none' }, }} >
                    
                        <MenuItem onClick={handleCloseNavMenu}>
                            <Typography textAlign="center"><Link to={'/'} style={{ textDecoration: 'none'}} >Home</Link></Typography>
                        </MenuItem>                
                    </Menu>
                </Box>
                <AccountBalanceIcon sx={{ display: { xs: 'flex', md: 'none' }, mr: 1 }} />
                <Typography variant="h5" noWrap component="a" href="/" sx={{ mr: 2, display: { xs: 'flex', md: 'none' }, flexGrow: 1, fontFamily: 'monospace', fontWeight: 700, letterSpacing: '.3rem', color: 'inherit', textDecoration: 'none', }} >
                    CSV
                </Typography>
                <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' } }}>
                    <Button onClick={handleCloseNavMenu} sx={{ my: 2, color: 'white', display: 'block' }} > <Link to="/" style={{textDecoration: 'none', color: 'white'}}>  Home </Link></Button> 
                </Box>

                <Box sx={{ flexGrow: 0 }}>
                    <Tooltip title="Profile">
                    <IconButton onClick={handleOpenUserMenu} sx={{ p: 0 }}>
                        <Avatar alt={user ? user?.name : GUser.name} src={ user ? '' : GUser.profileUrl} />
                    </IconButton>
                    </Tooltip>
                    <Menu sx={{ mt: '53px' }} id="menu-appbar" anchorEl={anchorElUser} anchorOrigin={{ vertical: 'top', horizontal: 'right', }}
                        keepMounted transformOrigin={{ vertical: 'top', horizontal: 'right', }} open={Boolean(anchorElUser)} onClose={handleCloseUserMenu} >
                    
                        <Box sx={{ width: '500px', flexGrow: 1}}>
                            <Typography variant='h5' component={'h5'} fontWeight={700} fontFamily={"cursive"} color={'primary'} align='center' >Profile</Typography>
                            <Divider sx={{ mt: 2, mb: 1}} />
                            <Box sx={{ m: 2, fontFamily: 'cursive', color: 'primary'}} >
                                <Typography variant='body1' component={'p'} color={'primary'} fontFamily={'cursive'} >Name: {user ? user?.name : GUser.name}</Typography>
                                <Typography variant='body1' component={'p'} color={'primary'} fontFamily={'cursive'} mt={0.5} >Email: {user ? user?.email : GUser.email}</Typography>
                            </Box>

                            <Divider sx={{ mt: 2, mb: 1}} />
                            <Box sx={{ mt: 1, fontFamily: 'cursive', color: 'primary', fontWeight: 500, width: '100%'}} >
                                {
                                    user ? <>
                                        <Link to={`/regsetPassword/${user?._id}`} style={{ textDecoration: 'none', color: '#5969f2', fontFamily: 'cursive',}} >
                                            <MenuItem onClick={handleCloseUserMenu} sx={{height: '50px'}}>Reset Password</MenuItem>
                                        </Link>
                                    </> : ''
                                }
                                <center><Button variant='outlined' onClick={logout} sx={{ fontFamily: 'cursive'}} >Logout</Button></center>
                            </Box>
                        </Box>
                    </Menu>
                </Box>
            </Toolbar>
        </Container>
    </AppBar>
    )
}

export default NavBar